body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#553055;
}

.App{
  width: 90%;
  background:#4c2a4c;
  max-width: 700px;
  color:#eee;
  margin:20px auto;
}

.navbar{
  text-align: center;
  padding:10px 20px;
  background:#6d3d6d;
}

.navbar h1{
  margin:10px 0;
}

.book-list{
  margin:20px;
}

.book-list ul{
  list-style-type: none;
  padding:0;
}

.book-list li {
  background: #6d3d6d;
  padding:10px;
  border-radius:4px;
  margin:10px 0;
  cursor: pointer;
}

.book-list li:hover{
  opacity: 0.7;
  text-decoration: line-through;
}

.book-list .title{
  font-weight: bold;
  color:#fff;
  font-size:1.2em;
}

.book-list .author{
  font-size: 0.9em;
  color:#ddd;
}

.empty{
  text-align: center;
  margin:20px;
}

form{
 padding:20px;
}

input[type='text']{
  width:100%;
  padding:10px;
  box-sizing:border-box;
  margin:6px 0;
  background: #3c1f3c;
  color:#fff;
  border:none;
}

input[type='submit']{
  margin:10px auto;
  display: block;
  background:rgb(218, 164, 164);
  padding:6px 20px;
  border:none;
  font-size:1.2em;
}
